	module.exports = {"data":{"markdownRemark":{"html":"<h3>Context</h3>\n<p>When a vulnerability is found, it needs to get into the right hands quickly to ensure it'll be resolved safely without public harm. Organizations typically publish a vulnerability disclosure policy with guidance on how they want to receive information related to potential vulnerabilities in their products or online services (see <a href=\"https://www.iso.org/standard/45170.html\">ISO 29147</a>). Some organizations, however, don't have well-defined methods of receiving vulnerability reports from external finders.</p>\n<p>In the absence of a vulnerability disclosure policy, attempts to report security vulnerabilities often carry considerable legal risk for the hacker, causing many to simply withhold vulnerable information or publish anonymously. In these cases, it's impossible to achieve an optimal outcome that ensures security vulnerabilities are safely resolved, and it causes the internet to be less safe than it could be.</p>\n<p>HackerOne provides Disclosure Assistance to help friendly hackers disclose vulnerabilities to any organization and to help create better security for the internet.</p>\n<blockquote>\n<p><strong>Note:</strong> Reports with Disclosure Assistance have no effect to your reputation.</p>\n</blockquote>\n<h3>What is Disclosure Assistance?</h3>\n<p>When hackers discover a vulnerability and the organization doesn't have a vulnerability disclosure policy, with Disclosure Assistance, HackerOne will work with friendly hackers on a best effort basis to:</p>\n<ul>\n<li>verify the legitimacy of a vulnerability.</li>\n<li>reach out to and verify the identity of an individual at the affected organization.</li>\n<li>share the vulnerability with the organization so it can be resolved.</li>\n</ul>\n<p>A hacker should <a href=\"https://hackerone.com/disclosure-assistance/disclosure_assistance_requests/new\">request Disclosure Assistance</a> after following these steps:</p>\n<ol>\n<li>They find a vulnerability.</li>\n<li>They search the <a href=\"https://hackerone.com/directory\">HackerOne Directory</a> for a published security contact method and attempt alternative means of contact.</li>\n<li>They exhaust their options in their attempts to contact the organization.</li>\n</ol>\n<h3>How Does it Work?</h3>\n<p>To request Disclosure Assistance:</p>\n<ol>\n<li>Go to <a href=\"https://hackerone.com/disclosure-assistance\">https://hackerone.com/disclosure-assistance</a>.</li>\n<li>Click <strong>Request Disclosure Assistance</strong>.</li>\n<li>Fill out the report form.</li>\n<li>Click <strong>Submit Report</strong>.</li>\n<li>The HackerOne Disclosure Assistance team receives the vulnerability information and verifies the legitimacy of the bus and determines the potential impact.</li>\n<li>HackerOne will attempt to contact the affected organization and verify the identity of an appropriate point of contact to receive the vulnerability information.</li>\n<li>Once their identity is verified, an email is sent to the point of contact with a secret link to the contents of the bug report and the interactions between the hacker and HackerOne. At this point, the vulnerability information has been successfully shared with the affected organization.</li>\n<li>\n<p>The point of contact can either:</p>\n<ul>\n<li>Create an account on HackerOne to interact with the finder directly or provide updates on the resolution of the vulnerability.</li>\n<li>Contact da@hackerone.com for assistance on how to proceed.</li>\n</ul>\n</li>\n</ol>\n<p>At the end of this process, HackerOne will inquire about the organization's preferred vulnerability disclosure process (based on ISO 29147) to avoid the need for Disclosure Assistance in the future.</p>\n<blockquote>\n<p><i>Note: As Disclosure Assistance is a best effort service, HackerOne prioritizes which bugs to assist with based on impact and may be unable to assist with low impact bugs. Please be aware that we cannot guarantee success, so we recommend familiarizing yourself with the EFF's Vulnerability Reporting FAQ and encourage you to perform other contact attempts in parallel to our effort.</i></p>\n</blockquote>\n<h3>Questions?</h3>\n<p>Questions specific to a particular report should be asked on the report itself. If you need support or have questions on the Disclosure Assistance process, please contact da@hackerone.com.</p>","frontmatter":{"title":"Disclosure Assistance","id":"hackers/disclosure-assistance","path":"/hackers/disclosure-assistance.html","description":null}}},"pathContext":{}}

